.item{
    width: 250px;
}
.item img{
    width: 250px;
}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 20px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;

}
.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
    
}
@media(max-width:1280px){
    .item{
        width: 220px;
        font-size: 14px;
    }
    .item img{
        width: 220px;

    }
    .item-price-old{
        font-size: 18px;
    }

}
@media(max-width:1024px){
    .item{
        width: 200px;
        font-size: 13px;
        
    }
    .item img{
        width: 150px;

    }
    .item-price-old{
        font-size: 15px;

    }
    .item-price-new{
        font-size: 15px;
    }
}
@media(max-width:800px){
    .item{
        width: 150px;
        font-size: 10px;
        
    }
    .item img{
        width: 120px;

    }
    .item-price-old{
        font-size: 12px;

    }
    .item-price-new{
        font-size: 12px;
    }
}
@media(max-width:500px){
    
    .item{
        width: 170px;
        font-size: 9px;
        padding: -20px;
        margin: -10px;
    }
    .item img{
        width: 120px;
    }
    .item-price-old{
        font-size: 10px;

    }
    .item-price-new{
        font-size: 10px;
    }
}
@media(max-width:375px){
    
    .item{
        width: 150px;
        font-size: 9px;
        padding: -20px;
        margin: -10px;
        
    }
    .item img{
        width: 115px;
        
    }
    .item-price-old{
        font-size: 10px;

    }
    .item-price-new{
        font-size: 10px;
    }
}