.descriptionbox{
    margin: 100px   170px;
}
.descriptionbox-navigator{
    display: flex;
}
.descriptionbox-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    border: 1px solid #d0d0d0;
    width: 151px;
    height: 60px;
}
.descriptionbox-nav-box.fade{
    background: #fbfbfb;
    color: #555;

} 
.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 1px solid #d0d0d0;
    padding: 38px;
    padding-bottom: 40px;

}
@media(max-width:1280px){
    .descriptionbox{
        margin: 60px;
    }
}
@media(max-width:800px){
    .descriptionbox{
        margin: 60px 30px;
    }
    .descriptionbox-description{
        font-size: 14px;
    }
}