.hero{
    height: 100vh;
    background: linear-gradient(180deg,#fdd275,#e1ffea22 60%);
    display: flex;
    
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
    
}
.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p{
    color: #171717;
    font-size: 50px;
    font-weight: 700;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img{
    height: 70px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 220px;
    height: 55px;
    border-radius: 75px;
    margin-top: 25px;
    background: #ff4141;
    color: white;
    font-size: 18px;
    font-weight: 500;
}
.hero-right {
    flex: 1;
    align-items: center;
    justify-content: center;
    display: flex;
    

}
.hero-right img{
    height: 450px;
    
}
@media(max-width:1280px){
    .hero-left{
        padding-left: 100px;    
    }
    .hero-left h2{
        font-size: 23px;

    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon img{
        width: 70px;
    }
    .hero-latest-btn{
        gap: 10px;
        width: 220px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img{
        width: 700px;
    }
}
@media(max-width:1024px){
    .hero-left{
        padding-left: 80px;    
    }
    .hero-left h2{
        font-size: 18px;

    }
    .hero-left p{
        font-size: 35px;
    }
    .hero-hand-icon img{
        width: 55px;
        height: 50px;
    }
    .hero-latest-btn{
        width: 190px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img{
        width: 570px;
    }
}
@media(max-width:800px){
    .hero-left{
        padding-left: 50px;    
    }
    .hero-left h2{
        font-size: 16px;

    }
    .hero-left p{
        font-size: 30px;
    }
    .hero-hand-icon img{
        width: 45px;
        height: 40px;
    }
    .hero-latest-btn{
        width: 155px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img{
        width: 450px;
    }
}
@media(max-width:500px){
    .hero{
        flex-direction: column;
    }
    .hero-right{
        display: none;
    }
    .hero-left{
        padding-left: 50px;    
    }
    .hero-left h2{
        font-size: 16px;

    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon img{
        width: 65px;
        height: 55px;
    }
    .hero-latest-btn{
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
    .hero-right img{
        width: 450px;
    }
}
@media(max-width:375px){
    .hero{
        flex-direction: column;
        padding-top: -10px;
        height: 60vh;
    }
    .hero-right{
        display: none;
    }
    .hero-left{
        padding-left: 40px;    

    }
    .hero-left h2{
        font-size: 16px;

    }
    .hero-left p{
        font-size: 40px;
    }
    .hero-hand-icon img{
        width: 55px;
        height: 50px;
    }
    .hero-latest-btn{
        width: 180px;
        height: 45px;
        font-size: 14px;
        margin-top: 0px;
    }
    
}